.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

input::-webkit-datetime-edit {
  display: block;
  padding: 0;
}

input::-webkit-date-edit {
  display: block;
  padding: 0;
}
input::-webkit-date {
  display: block;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

.scroll-section {
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

@font-face {
  font-family: 'Barcelony';
  src: url('/src/assets/fonts/Barcelony.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

/* Créer une classe CSS pour appliquer cette police */
.font-ma-police {
  font-family: 'Barcelony', sans-serif;
}

@font-face {
  font-family: 'IndieFlower-Regular';
  src: url('/src/assets/fonts/IndieFlower-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

/* Créer une classe CSS pour appliquer cette police */
.indie-flower-regular {
  font-family: 'IndieFlower-Regular', sans-serif;
}